<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5

    .titulo-principal
      .titulo-principal__numero
        span 1
      h1 Inventarios

    p.mb-5 Antes de hablar del registro de compras y ventas, es importante enfocarse en el tema de los inventarios para la venta, produccin o prestacin de servicios, toda vez que constituyen la fuente generadora de ingresos para la empresa y por tanto requieren de un tratamiento especial.
    
    #t_1_1.titulo-segundo
      .h2 1.1 Poltica contable

    p Dentro del manual de polticas contables establecido por la empresa es de especial importancia el captulo correspondiente a los inventarios, pues a travs de este se establecen los lineamientos a seguir para la determinacin del sistema y mtodo de valuacin de los inventarios y su correspondiente tratamiento en las diferentes operaciones.

    p De igual forma, la poltica contable de inventarios debe establecer los gastos correlacionados en una venta, donde lo principal corresponde al costo de venta, pues permite determinar el resultado de la operacin.

    p Otro aspecto fundamental que debe incluirse en la poltica de inventarios es el establecimiento de los precios de venta, pues la seccin 13 de NIIF para Pymes (2009), requiere que se haga una comparacin entre el costo de adquisicin y los egresos necesarios para ponerlo en condiciones de venta y el precio de venta neto estimado, para determinar los costos de venta del inventario.

    p.mb-5 Adems de las compras, ventas de inventarios y las devoluciones que de estas se desprenden, tambin hacen parte de las polticas contables, los traslados entre bodegas y los procesos para el tratamiento de deterioros, y bajas de inventarios. 

    p.mb-4 De conformidad con la NIC 8 para el diseo de las polticas contables, es importante tener presente entre otros los siguientes elementos:
    

    .row.justify-content-center.align-items-center
      .col-lg-6       
        ul.lista-ul--color
          li 
            i.fas.fa-check
            | Una NIIF se aplica especficamente a una transaccin y las polticas contables aplicadas a esa partida se determinarn con dicha NIIF. 
          li 
            i.fas.fa-check
            | En las NIIF se establecen polticas contables sobre las que el IASB (International Accounting Standards Board (Junta de Normas Internacionales de Contabilidad) ha llegado a la conclusin de que dan lugar a estados financieros con informacin relevante y fiable sobre las transacciones. 
          li
            i.fas.fa-check
            | Las NIIF se acompaan de guas que ayudan a las entidades a aplicar sus requerimientos. 
          li 
            i.fas.fa-check
            | En ausencia de una NIIF que sea aplicable especficamente a una transaccin, la gerencia deber usar su juicio en el desarrollo y aplicacin de una poltica contable, a fin de suministrar informacin que sea: relevante, fiable, fidedigna, neutral, prudente y reflejen la esencia econmica de las transacciones y no simplemente su forma legal.
          li 
            i.fas.fa-check
            | La gerencia se referir a las fuentes para considerar su aplicabilidad; como los requerimientos de temas relacionados y las definiciones, reconocimiento y medicin para los elementos de los estados financieros.
         
               
      .col-lg-6.mb-5
        img(src="@/assets/curso/images/tema1/img_1.svg" alt="", style="width:450px; display:block; margin: 0 auto;")

    p.mt-5 Por otro lado, es importante tener en cuenta los siguientes aspectos contemplados por la NIC 2 para el correspondiente diseo de la poltica contable de los inventarios: 
      
    .row.justify-content-center.align-items-center.mt-5
      .col-6.col-md-8.col-lg-6.mb-5
        img(src="@/assets/curso/images/tema1/img_2.svg" alt="", style="width:400px; display:block; margin: 0 auto;")    
      .col-12.col-md-8.col-lg-6 
        ul.lista-ul--color
          li 
            i.fas.fa-check
            | Deben ser considerados como activos posedos para ser vendidos en el curso normal de la operacin.
          li 
            i.fas.fa-check
            | Identificados como elementos del proceso de produccin con destino a ser vendido          
          li 
            i.fas.fa-check
            | Establecidos como materiales o suministros que sern consumidos en el proceso de produccin o en la prestacin de servicios. 
          li 
            i.fas.fa-check
            | Valor neto realizable es el precio estimado de venta de un activo en el curso normal de la operacin menos los costos estimados para terminar su produccin y los necesarios para llevar a cabo la venta. 
          li 
            i.fas.fa-check
            | Valor razonable es el precio que se recibira por vender un activo o que se pagara por transferir un pasivo en una transaccin ordenada entre participantes del mercado en la fecha de la medicin. 
          li 
            i.fas.fa-check
            | Entre los inventarios tambin se incluyen los bienes comprados y almacenados para su reventa, entre los que se encuentran, por ejemplo, las mercaderas adquiridas por un minorista para la reventa a sus clientes, y tambin los terrenos u otras propiedades de inversin que se tienen para ser vendidos a terceros. 
          li 
            i.fas.fa-check
            | Tambin son inventarios los productos terminados o en curso de fabricacin mantenidos por la entidad, as como los materiales y suministros para ser usados en el proceso productivo.
    Separador 
    #t_1_2.titulo-segundo
      .h2 1.2 Concepto, tipos de inventario, valuacin de inventarios

    p.mb-3 De conformidad con la NIC 2, se definen los diferentes tipos de costos, de la siguiente manera:              
    .jumbotron-blue.p-4.mb-5
      .row.justify-content-center.align-items-center.mt-5
        .col-lg-5.mb-4.mb-lg-0
          img(src="@/assets/curso/images/tema1/img_3.svg" alt="", style="width:450px; display:block; margin: 0 auto;")
        .col-lg-7
          .col-sm.mt-3
            ul.lista-ul--color
              li 
                i.fas.fa-check
                | Los inventarios se medirn al costo o al valor neto realizable, segn cul sea menor. 
              li 
                i.fas.fa-check
                | El costo de los inventarios comprender todos los costos derivados de su adquisicin, transformacin, as como otros costos en los que se haya incurrido para darles su condicin y ubicacin actuales. 
              li
                i.fas.fa-check
                | El costo de adquisicin de los inventarios comprender el precio de compra, los aranceles de importacin y otros impuestos (que no sean recuperables posteriormente de las autoridades fiscales) y transporte, manejo y otros costos directamente atribuibles a la adquisicin de mercaderas, materiales y servicios. Los descuentos comerciales, las rebajas y otras partidas similares se deducirn para determinar el costo de adquisicin. 
              li
                i.fas.fa-check
                | Los costos de transformacin de los inventarios comprendern a aquellos costos relacionados con las unidades de produccin, tales como la materia prima directa, la mano de obra directa y los costos indirectos de produccin. 
              li 
                i.fas.fa-check
                | Son costos indirectos fijos de produccin los que permanecen relativamente constantes, con independencia del volumen de produccin, tales como la depreciacin y mantenimiento de los edificios y equipos de la fbrica y los activos por derecho de uso utilizados en el proceso de produccin.         

    p De acuerdo con la seccin 13 de NIIF para Pymes (2009), los inventarios son activos:

    .row.justify-content-center.align-items-center.my-5
      .col-12
        img(src="@/assets/curso/images/tema1/img_4.svg" alt="", style="width:850px").m-auto      

    p La necesidad de controlar los inventarios requiere la aplicacin de un sistema que permita determinar el costo de existencia y el costo de venta; es as como se debe elegir entre dos opciones, el sistema peridico y el sistema permanente.    

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-5.col-xl-6
        p El sistema de inventarios peridico tiene las siguientes caractersticas:
        ol.lista-ol--cuadro
          li 
            .lista-ol--cuadro__vineta
              span 1
            | Se hace conteo fsico para hallar el costo de existencia.
          li 
            .lista-ol--cuadro__vineta
              span 2
            | Solo al cierre del ejercicio se halla el costo de venta. 
          li 
            .lista-ol--cuadro__vineta
              span 3
            | Solo hay un mtodo de valuacin Juego de Inventarios.
          li 
            .lista-ol--cuadro__vineta
              span 4
            | El krdex solo mueve unidades sin costos.
         
    
        p.my-4 El sistema de inventarios permanente tiene las siguientes caractersticas:

        ol.lista-ol--cuadro
          li 
            .lista-ol--cuadro__vineta
              span 1
            | Se hace conteo fsico por control y no para hallar costos.
          li 
            .lista-ol--cuadro__vineta
              span 2
            | Costos de venta y existencia se hallan en cualquier momento.         
          li 
            .lista-ol--cuadro__vineta
              span 3
            | Se debe elegir uno entre varios mtodos de valuacin Promedio ponderado, Primeras en Entrar Primeras en Salir.
          li 
            .lista-ol--cuadro__vineta
              span 4
            | El krdex mueve tanto unidades como costos.
          
      .col-lg-7.col-xl-6
        img(src="@/assets/curso/images/tema1/img_5.svg" alt="", style="width:500px;").m-auto 

