      .h2 1.3 Medicin, reconocimiento con impuestos y retenciones.
    .row.justify-content-center.align-items-center.mb-4
      .col-12.col-lg-6
        p La medicin de los inventarios consiste en determinar el valor por el cual sern reconocidos en los estados financieros. La NIC 2, hace referencia de las tcnicas para la medicin inicial y posterior del costo de los inventarios, tales como el mtodo del costo estndar o el mtodo de los minoristas, los cuales podrn ser utilizadas por conveniencia siempre que el resultado de aplicarlas se aproxime al costo.
        p Los costos estndares tendrn en cuenta los niveles normales de materias primas, suministros, mano de obra, eficiencia y utilizacin de la capacidad. Estos se revisarn de forma regular y, si es necesario, se cambiarn en funcin de las condiciones actuales. 

        p El mtodo de los minoristas se utiliza a menudo, en el sector comercial al por menor para la medicin de inventarios cuando hay un gran nmero de artculos que rotan velozmente, que tienen mrgenes similares y para los cuales resulta impracticable usar otros mtodos de clculo de costos. Cuando se emplea este mtodo, el costo de los inventarios se determinar deduciendo, del precio de venta del artculo en cuestin, un porcentaje apropiado de margen bruto. El porcentaje aplicado tendr en cuenta la parte de los inventarios que se han marcado por debajo de su precio de venta original. A menudo se utiliza un porcentaje promedio para cada seccin o departamento comercial.       

      .col-8.col-md-8.col-lg-6
        img(src="@/assets/curso/images/tema1/img_9.svg" alt="", style="width:450px; display:block; margin: 0 auto;")

    .cajon.color-custom.p-4.mb-4
      p.mb-0 De acuerdo con la seccin 13 de NIIF para Pymes (2009), una entidad medir los inventarios al importe menor entre el costo y el precio de venta estimado menos los costos de terminacin y venta. Una entidad incluir en el costo de los inventarios todos los costos de compra, costos de transformacin y otros costos incurridos para darles su condicin y ubicacin actuales. Se puede usar dos o ms columnas para diagramar el texto, estos elementos pueden presentar contenedores que permitan diferenciar ideas o conceptos.  

    p De acuerdo con la seccin 13 de NIIF para Pymes (2009), cuando los inventarios se vendan, la entidad reconocer el importe en libros de estos como un costo (gasto) en el perodo en el que se reconozcan los correspondientes ingresos de actividades ordinarias.

    p En el proceso de compra se debe reconocer el impuesto a las ventas descontable y practicar las correspondientes retenciones sobre los impuestos a la renta, a industria y comercio e incluso a las ventas; dependiendo de las obligaciones tributarias y caractersticas fiscales del comprador y el proveedor.

    p Por otro lado, en el proceso de ventas se debe reconocer el impuesto a las ventas generado, al igual que los correspondientes anticipos por retenciones de los impuestos a la renta, de industria y comercio e incluso a las ventas; dependiendo de las obligaciones tributarias y caractersticas fiscales del vendedor y el cliente.

    p.mb-5 A continuacin, se muestra un ejercicio de reconocimiento de las operaciones de compra y venta de inventarios incluido los impuestos y las retenciones.

